const video = document.querySelector('.about-section__video');

if (video) {
  const playBtn = document.querySelector('.about-section__play-btn');
  const blackout = document.querySelector('.about-section__blackout');

  playBtn.addEventListener("click", () => {
    playBtn.classList.add('disable');
    blackout.classList.add('disable');
    video.controls = true;
    video.play();
  })

  video.addEventListener("pause", () => {
    video.controls = false;
    playBtn.classList.remove('disable');
    blackout.classList.remove('disable');
  })
}