const tabNav = document.querySelector('.portfolio-tabs-nav'),
	   tabNavBtns = document.querySelectorAll('.portfolio-tabs-nav__btn');

const tabItems = document.querySelectorAll('.portfolio-tabs__item');

const loadMoreBtn = document.querySelector('.load-more');

const maxItems = 9;

if(document.querySelector('.portfolio-tabs-nav')) {
  const hideMoreItems = elements => {
    if(elements.length > maxItems) {
      const arr = Array.from(elements);
      const hideItems = arr.slice(maxItems);
  
      hideItems.forEach(el => {
        el.classList.add('portfolio-tabs__item--visible-more');
      })
  
      loadMoreBtn.style.display = 'flex';
    } else {
      loadMoreBtn.removeAttribute('style');
    }
  }
  
  window.addEventListener('load', () => {
    tabItems.forEach(el => { el.classList.add('portfolio-tabs__item--visible') });
    hideMoreItems(tabItems);
  })
  
  tabNav.addEventListener('click', event => {
    const target = event.target;
  
    if(target.classList.contains('portfolio-tabs-nav__btn')) {
      const path = target.dataset.path;
      const pathItems = document.querySelectorAll(`[data-target='${path}']`);
  
      tabNavBtns.forEach(el => { el.classList.remove('portfolio-tabs-nav__btn--active') });
      target.classList.add('portfolio-tabs-nav__btn--active');
  
      tabItems.forEach(el => {
        el.classList.remove('portfolio-tabs__item--visible');
        el.classList.remove('portfolio-tabs__item--visible-more');
      });
  
      pathItems.forEach(el => { el.classList.add('portfolio-tabs__item--visible') });
      hideMoreItems(pathItems);
  
      if(path == 'all') {
        tabItems.forEach(el => { el.classList.add('portfolio-tabs__item--visible') });
        hideMoreItems(tabItems);
      }
    }
  })
  
  loadMoreBtn.addEventListener('click', () => {
    tabItems.forEach(el => el.classList.remove('portfolio-tabs__item--visible-more'));
    loadMoreBtn.removeAttribute('style');
  })
}
